import { Component, OnInit, HostListener  } from '@angular/core';
import { Router } from '@angular/router';
import { ToolbarTextService } from '../services/toolbar-text.service';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.scss']
})


export class HomepageComponent implements OnInit {

 
 

  constructor(private router: Router,private toolbarTextChage: ToolbarTextService) { }

  ngOnInit(): void {
  }

  gotoaboutme(data:String){
    this.router.navigateByUrl('/About');
    this.toolbarTextChage.toolBarTextChange('About');
  }

  onScroll(event: any) {
    debugger;
    // visible height + pixel scrolled >= total height 
    if (event.target.offsetHeight + event.target.scrollTop >= event.target.scrollHeight) {
      console.log("End");
    }
  }

 
}
