import { ScrollDispatcher } from '@angular/cdk/scrolling';
import { Component, HostListener, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { ToolbarTextService } from '../services/toolbar-text.service';
import { AboutMe } from '../shared/allconst/Aboutme';

@Component({
  selector: 'app-aboutpage',
  templateUrl: './aboutpage.component.html',
  styleUrls: ['./aboutpage.component.scss'],
})
export class AboutpageComponent implements OnInit {
  aboumedata: any;


  constructor(
    private router: Router,
    private toolbarTextChage: ToolbarTextService,
    private scrollDispatcher: ScrollDispatcher
  ) {
    this.aboumedata = AboutMe.data;
  
  }

  ngOnInit(): void {}
  gotoskill() {
    this.router.navigateByUrl('/Skill');
    this.toolbarTextChage.toolBarTextChange('Skill');
  }

 
  
 
}
