import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { DatePipe } from '@angular/common';

@Injectable({
  providedIn: 'root'
})
export class ContactmeService {

  private dbPath = '/Contacts';

  contactref: AngularFireList<any>;

  constructor(private db: AngularFireDatabase,public datepipe: DatePipe) {
    this.contactref = db.list(this.dbPath);
   }

  create(contact: any): any {
    console.log(contact);
    let latest_date =this. datepipe. transform(new Date(), 'yyyy-MM-dd hh:mm a');
    contact.date = latest_date;
    return this.contactref.push(contact);
  }
}
