import { Injectable } from '@angular/core';

import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ToolbarTextService {
  private toolbarTextSubject = new Subject<String>();
  toolbarTextObservable = this.toolbarTextSubject.asObservable();
  constructor() { }

  toolBarTextChange(text: String){
      this.toolbarTextSubject.next(text);
  }

}
