import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { ContactmeService } from '../services/contactme.service';

@Component({
  selector: 'app-contacmepage',
  templateUrl: './contacmepage.component.html',
  styleUrls: ['./contacmepage.component.scss']
})
export class ContacmepageComponent implements OnInit {

  contactform: FormGroup;
  submitted = false;
  datasend:boolean = false;

  constructor(private formbulider: FormBuilder,private conatservice : ContactmeService) { }

  ngOnInit(): void {
    this.contactform = this.formbulider.group({
      firstname: [null, Validators.required],
      lastname: [null, Validators.required],
      email: [null, [
        Validators.required,
        Validators.pattern(
          /^(?=[^@]*[A-Za-z])([a-zA-Z0-9])(([a-zA-Z0-9])*([\._-])?([a-zA-Z0-9]))*@(([a-zA-Z0-9\-])+(\.))+([a-zA-Z]{2,4})+$/
        ),
      ]],
      subject: [null, Validators.required],
      organisation :[null],
      comment: [null, Validators.required]
    })
  }

  get f(){
    return this.contactform.controls;
  }

  filedownload(){
        let link = document.createElement("a");
        link.download = "Kunj_Patel_Resume";
        link.href = "assets/img/Kunj_Patel_Resume.pdf";
        link.click();
  }

  onSubmit(){
    this.submitted = true;
    if (this.contactform.invalid) {
      return;
    }
    this.conatservice.create(this.contactform.value).then(() => {
      console.log('Created new item successfully!');
    
      this.datasend = true;
    });
    // this.insertdata.postCompany(this.insertdataform.value).subscribe((data:any) => {
    //     if(data["Status Code"] == 201){
    //       this._snackBar.open("Data Inserted", "Successfully", {
    //         duration: 3000,
    //       });
    //       this.insertdataform.reset();
    //       this.submitted = false;
    //     }
    // });
    
   
  }

}
