import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SidebartoggleService {
  private drawerToggleSubject = new Subject<String>();
  drawerToggleObservable = this.drawerToggleSubject.asObservable();
  constructor() { }
  toggleSidebar(sidbarstatus:String) {
    this.drawerToggleSubject.next(sidbarstatus);
  }
}


