import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ToolbarTextService } from '../services/toolbar-text.service';
import { ProjectsData } from '../shared/allconst/Projects';

@Component({
  selector: 'app-projectpage',
  templateUrl: './projectpage.component.html',
  styleUrls: ['./projectpage.component.scss']
})
export class ProjectpageComponent implements OnInit {

  name:String = 'devicon-android-plain';
  allProjectsdata: any;
  constructor(private router: Router,private toolbarTextChage: ToolbarTextService) { }

  ngOnInit(): void {
    this.allProjectsdata = ProjectsData.data;
  }

  openSingleProject(projectName: String){
    this.router.navigateByUrl(`SingleProject/${projectName}`);
    this.toolbarTextChage.toolBarTextChange(projectName);
   
  }

}
