import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutpageComponent } from './aboutpage/aboutpage.component';
import { ContacmepageComponent } from './contacmepage/contacmepage.component';
import { HomepageComponent } from './homepage/homepage.component';
import { ProjectpageComponent } from './projectpage/projectpage.component';
import { SingleProjectComponent } from './single-project/single-project.component';
import { SkillpageComponent } from './skillpage/skillpage.component';

const routes: Routes = [
  {path: '', redirectTo: 'Home', pathMatch: 'full'},
  {path: 'Home', component: HomepageComponent, data: { animation: 0 }},
  {path: 'About', component: AboutpageComponent, data: { animation:  1}},
  {path: 'Skill', component: SkillpageComponent, data: { animation: 2 }},
  {path: 'Contact', component: ContacmepageComponent, data: { animation: 3 }},
  {path: 'Projects', component: ProjectpageComponent, data: { animation: 4 }},
  {path: 'SingleProject/:ProjectName', component: SingleProjectComponent, data: { animation: 5 }},
  {path: 'ContactMe', component: ContacmepageComponent, data: { animation: 6 }},
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { anchorScrolling: 'enabled' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
