import { DOCUMENT } from '@angular/common';
import { Component, HostListener, Inject, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { ToolbarTextService } from '../services/toolbar-text.service';
import { SidebartoggleService } from '../shared/services/sidebartoggle.service';

@Component({
  selector: 'app-toolbar',
  templateUrl: './toolbar.component.html',
  styleUrls: ['./toolbar.component.scss']
})
export class ToolbarComponent implements OnInit {
  openSidnav:Boolean = false;
  private toggleSubscription : Subscription;
  private toolbarSubscription : Subscription;
  title: String = 'Home';
  isClose:Boolean = false;
  elem: any;
  isFullScreen: boolean;
  constructor(@Inject(DOCUMENT) private document: any,private toggleService: SidebartoggleService,private router: Router,private toolbarTitle: ToolbarTextService) { }

  ngOnInit(): void {
    this.chkScreenMode();
    this.elem = document.documentElement;
    console.log(this.router.routerState.snapshot.url);
    this.toggleSubscription = this.toggleService.drawerToggleObservable.subscribe((data:String) =>{
     
      if(data ==='open'){
        this.openSidnav = true;
      }
      else{
        this.openSidnav = false;
      }
    });
    this.toolbarSubscription = this.toolbarTitle.toolbarTextObservable.subscribe((data: String) => {
        this.closeCheck(data);
        this.title = data;
    });
  }

     @HostListener('document:fullscreenchange', ['$event'])
     @HostListener('document:webkitfullscreenchange', ['$event'])
     @HostListener('document:mozfullscreenchange', ['$event'])
     @HostListener('document:MSFullscreenChange', ['$event'])

     chkScreenMode(){
      if(document.fullscreenElement){
        //fullscreen
        this.isFullScreen = true;
      }else{
        //not in full screen
        this.isFullScreen = false;
      }
    }

    CloseBtn(){
        this.router.navigateByUrl('/Projects');
        this.toolbarTitle.toolBarTextChange('Projects');
    }

    closeCheck(data:String){
      if(data != 'Home' && data !="About" && data !="Skill" && data !="Projects" && data !="Contact Me"){
        this.isClose  = true;
      }else{
        this.isClose  = false;
      }
    }

  openFullscreen() {
    if (this.elem.requestFullscreen) {
      this.elem.requestFullscreen();
    } else if (this.elem.mozRequestFullScreen) {
      /* Firefox */
      this.elem.mozRequestFullScreen();
    } else if (this.elem.webkitRequestFullscreen) {
      /* Chrome, Safari and Opera */
      this.elem.webkitRequestFullscreen();
    } else if (this.elem.msRequestFullscreen) {
      /* IE/Edge */
      this.elem.msRequestFullscreen();
    }
  }

  closeFullscreen() {
    if (this.document.exitFullscreen) {
      this.document.exitFullscreen();
    } else if (this.document.mozCancelFullScreen) {
      /* Firefox */
      this.document.mozCancelFullScreen();
    } else if (this.document.webkitExitFullscreen) {
      /* Chrome, Safari and Opera */
      this.document.webkitExitFullscreen();
    } else if (this.document.msExitFullscreen) {
      /* IE/Edge */
      this.document.msExitFullscreen();
    }
  }

  gotohome() {
    this.router.navigateByUrl('/Home');
    this.toolbarTitle.toolBarTextChange('Home');
  }


  toggleSidenav(){
    
    this.openSidnav = !this.openSidnav;
    if(this.openSidnav){
      this.toggleService.toggleSidebar("open");
    }
    else{
      this.toggleService.toggleSidebar("close");
    }
    
  }

}
