import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDrawer } from '@angular/material/sidenav';
import { NavigationEnd, Router, RouterOutlet } from '@angular/router';
import { Subscription } from 'rxjs';
import { ToolbarTextService } from '../services/toolbar-text.service';
import { slideInAnimation } from '../shared/animations/slideanimation';
import { SidebartoggleService } from '../shared/services/sidebartoggle.service';

@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.scss'],
  animations:[slideInAnimation]
})
export class SidenavComponent implements OnInit {
  showFiller:Boolean = false;
  private toggleSubscription : Subscription;

  @ViewChild('drawer') drawer: MatDrawer;

  constructor(private toggleService: SidebartoggleService,private router: Router,private toolbarTextChage: ToolbarTextService) { }

  ngOnInit(): void {
  
    this.toggleSubscription = this.toggleService.drawerToggleObservable.subscribe((data:String) =>{
      
      this.sidenavtoggele(data);
    })
  }

  closeSideNav(){
    this.toggleService.toggleSidebar("close");
  }
  closeSideNavRoute(page:String){
    this.toggleService.toggleSidebar("close");
    switch(page){
      case 'Home':
        this.router.navigateByUrl('/Home');
        this.toolbarTextChage.toolBarTextChange('Home');
        break;
      
      case 'About':
        this.router.navigateByUrl('/About');
        this.toolbarTextChage.toolBarTextChange('About');
        break;
      
      case 'Skill':
        this.router.navigateByUrl('/Skill');
        this.toolbarTextChage.toolBarTextChange('Skill');
        break;
     case 'Projects':
        this.router.navigateByUrl('/Projects');
        this.toolbarTextChage.toolBarTextChange('Projects');
        break;

      case 'Conatct':
        this.router.navigateByUrl('/ContactMe');
        this.toolbarTextChage.toolBarTextChange('Contact Me');
        break;

    }

  }

  sidenavtoggele(data:String){
    if(data === "open"){
      this.drawer.open();
    }
    else{
      this.drawer.close();
    }
    
  }

  prepareRoute(outlet: RouterOutlet) {
    return outlet && outlet.activatedRouteData && outlet.activatedRouteData.animation;
  }

  onActivate(event: any) {
  const scrollElem: any = document.querySelector('#moveTop');
  if(scrollElem){
    scrollElem.scrollIntoView();   
  }
  }



}
